                        x: x,
                        n: n,
                        isEmpty: isEmpty,
                        symbol: curSymbol,
                        symbolSize: data[i].symbolSize || symbolSize,
                        color: data[i].color,
                        borderColor: data[i].borderColor,
                        borderWidth: data[i].borderWidth,
                        name: this._getReformedLabel(i),
                        textColor: dataTextStyle.color,
                        textAlign: textAlign,
                        textBaseline: dataTextStyle.baseline || 'middle',
